.navlinks {
    margin: 0 25px 0 0;
    font-size: 17px;
    font-weight: 400;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}
.navlinks:last-child {
    margin: 0;
}
.mobile_nav {
    display: none;
    &_btn {
        display: none;
        div {
            position: relative;
            z-index: 4;
            transform-origin: center;
            width: 40px;
            height: 3px;
            margin: 7px 0;
            background: rgb(234, 0, 255);
            transition: 1s;
            -webkit-transition: 1s;
            -moz-transition: 1s;
            -ms-transition: 1s;
            -o-transition: 1s;
        }
        .menu_btn_anim1 {
            margin-top: 20px;
            transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
            -moz-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            -o-transform: rotate(45deg);
        }
        .menu_btn_anim2 {
            opacity: 0;
        }
        .menu_btn_anim3 {
            margin-top: -20px;
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
        }
    }
    &_item {
        position: absolute;
        display: none;
        flex-direction: column;
        right: 0;
        background: #060f3d;
        top: 0;
        z-index: 3;
        padding: 120px 10px 30px 35px;
        box-shadow: 0 0 3px 5px #060f3d, 
        0 0 100px 7px #060f3d
        0 0 7px 10px #060f3d,
        0 0 9px 15px #060f3d;
        .navlinks {
            font-size: 23px;
        }
    }
}

@media screen and (max-width: 700px) {
    .desktop_nav {
        display: none;
    }
    .mobile_nav {
        display: block;
        &_btn {
            display: block;
        }
    }
}
